/* settings */
$burder-color: #fff
$burder-active-color: #fff
.hamburger
  padding: 0
  display: none
  align-items: center
  justify-content: center
  cursor: pointer
  transition-property: opacity, filter
  transition-duration: 0.15s
  transition-timing-function: linear
  font: inherit
  color: inherit
  text-transform: none
  border: 0
  overflow: visible
  position: relative
  width: 50px
  height: 50px
  border-radius: 50%
  z-index: 20
  margin: 0 0 0 50px
  background-color: $accent-color
  @media (max-width: 959px)
    display: inline-flex
  @media (max-width: 575px)
    margin: 0 0 0 20px
  &.is-active
    .hamburger-inner
      background-color: $burder-active-color
      &::before, &::after
        background-color: $burder-active-color
.hamburger-box
  width: 20px
  height: 12px
  display: inline-block
  position: relative
.hamburger-inner
  display: block
  top: 50%
  width: 20px
  height: 2px
  background-color: $burder-color
  border-radius: 4px
  position: absolute
  transition-timing-function: ease
  transition: transform .15s
  &::before, &::after
    width: 20px
    height: 2px
    background-color: $burder-color
    border-radius: 4px
    position: relative
    transition-property: transform
    transition-duration: 0.15s
    transition-timing-function: ease
  &::before, &::after
    content: ""
    display: block
  &::before
    top: -10px
    display: none
/* slide animate */
.hamburger--slider
  .hamburger-inner
    top: 0px
    &::before
      top: 8px
      right: 0
      width: 32px
      transition-property: transform, opacity
      transition-timing-function: ease
      transition-duration: 0.15s
    &::after
      top: 10px
  &.is-active .hamburger-inner
    transform: translate3d(-2px,5px,0) rotate(45deg)
    height: 2px
    width: 25px
    &::before
      transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0)
      opacity: 0
    &::after
      transform: translate3d(0, -10px, 0) rotate(-90deg)
      height: 2px
      width: 25px