html {
  margin: 0 !important;
}

* {
  box-sizing: border-box;
  outline: none;
}
*::before, *::after {
  box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd {
  margin: 0;
}

ul[class], ol[class] {
  padding: 0;
  list-style-type: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: inherit;
}
a[class] {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

button, label {
  cursor: pointer;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  position: relative;
  font-family: GardensC, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 29px;
  color: #4D4D4D;
  background-color: #F2E9E0;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #BC8548;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #D74F29;
}
body::-webkit-scrollbar-track {
  background: #F2E9E0;
  border-radius: 0;
}
body {
  scrollbar-face-color: #BC8548;
  scrollbar-track-color: #F2E9E0;
}
@media (max-width: 1199px) {
  body {
    font-size: 14px;
    line-height: 22px;
  }
}
body.overflow {
  overflow: hidden;
  padding-right: var(--scroll-width);
}
body.overflow .header {
  padding-right: var(--scroll-width);
}
body.overflow.menu-show:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 11;
}

