.preloader {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}
.preloader__content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
}
.preloader__content .logo {
  display: block;
  width: auto;
  max-width: 100%;
  margin: 0 auto;
  transition: transform 0.5s, opacity 0.5s;
}
.preloader__progress-bar {
  width: calc(100% - 30px);
  height: 1px;
  max-width: 800px;
  background-color: #C2C2C2;
  margin: 60px 15px 0;
  transition: transform 0.5s, opacity 0.5s;
  position: relative;
}
.preloader__progress-bar .current-line {
  display: block;
  height: 3px;
  width: 0;
  background-color: #D74F29;
  position: absolute;
  bottom: -1px;
  left: 0;
  transition: width 0.5s;
}
.preloader__percent {
  text-align: center;
  font-family: Oswald, sans-serif;
  font-weight: 300;
  font-size: 30px;
  line-height: 100%;
  color: #4D4D4D;
  margin-top: 45px;
  transition: color 0.3s, transform 0.5s, opacity 0.5s;
}
.preloader__percent.done {
  color: #D74F29;
}
.preloader.hide .preloader__content .logo {
  transform: translateY(-10px);
  opacity: 0;
}
.preloader.hide .preloader__progress-bar {
  transform: translateY(-10px);
  opacity: 0;
}
.preloader.hide .preloader__percent {
  transform: translateY(-10px);
  opacity: 0;
}

