@import "settings"
@import "_mixins/_mixins"
@import "_fonts/fonts_list"
@import "_fonts/fontello"                                                                                               // if you use fontello fonts
@import "_components/reset"
@import "_components/header"

/* main page */
.main-page
  background-repeat: no-repeat
  background-position: top center
  background-size: cover
  &__title
    font-family: Oswald, sans-serif
    font-weight: 300
    font-size: 90px
    line-height: 120%
    @media (max-width: 1335px)
      font-size: 72px
    @media (max-width: 1199px)
      font-size: 60px
    @media (max-width: 575px)
      font-size: 40px
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    height: calc(100vh - 109px)
    min-height: 800px
    @media (max-height: 800px)
      min-height: calc(768px - 109px)
    @media (max-width: 1199px)
      min-height: 510px
      height: 62vh
    @media (max-width: 959px)
      height: calc(100vh - 70px)
      flex-direction: column
    &__left-side
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-end
      flex: 0 0 55%
      max-width: 55%
      color: #fff
      padding-bottom: calc(5% - 10px)
      padding-right: 20px
      @media (max-width: 1199px)
        flex: 0 0 50%
        max-width: 50%
        padding-bottom: 60px
      @media (max-width: 1023px)
        padding-right: 0
      @media (max-width: 959px)
        flex: 0 0 auto
        max-width: 450px
        margin-top: auto
        padding: 0 0 40px
    &__right-side
      flex: 0 0 45%
      max-width: 45%
      position: relative
      @media (max-width: 1199px)
        margin-left: auto
      @media (max-width: 959px)
        flex: 0 0 auto
        max-width: calc(100% + 122px)
        margin: 0 -61px
      @media (max-width: 767px)
        max-width: calc(100% + 40px)
        margin: 0 -20px
      @media (max-width: 639px)
        max-width: calc(100% + 30px)
        margin: 0 -15px
      .main-slider
        position: absolute
        height: calc(100% + 116px)
        width: 588px
        bottom: 0
        right: 0
        z-index: 10
        padding: 50px 0
        @media (max-width: 1800px)
          height: 100%
          padding: 0
        @media (max-width: 1335px)
          max-width: 525px
        @media (max-width: 1199px)
          max-width: 370px
          padding: 20px 0
        @media (max-width: 959px)
          max-width: 100%
          width: 100%
          position: relative
          height: auto
          padding: 50px 61px
          width: 100%
        @media (max-width: 767px)
          padding: 50px 20px
        @media (max-width: 639px)
          padding: 40px 15px 30px
        .slide-item
          display: flex
          align-items: stretch
          justify-content: flex-start
          height: 344px
          @media (max-width: 1199px)
            height: auto
          @media (max-width: 959px)
            max-width: 258px
          @media (max-width: 575px)
            max-width: 238px
          &:nth-of-type(3n+1)
            .descr-block
              background-color: $accent-color
          &:nth-of-type(2n)
            .descr-block
              background-color: #BC8548
          &:nth-of-type(3n)
            .descr-block
              background-color: #2D3E72
          .descr-block
            flex: 0 0 calc(100% - 220px)
            max-width: calc(100% - 220px)
            color: #fff
            padding: 50px
            display: flex
            flex-direction: column
            align-items: flex-start
            justify-content: flex-start
            @media (max-width: 1335px)
              padding: 30px
            @media (max-width: 1199px)
              flex: 0 0 calc(100% - 132px)
              max-width: calc(100% - 132px)
              padding: 40px 30px 35px
            @media (max-width: 1023px)
              padding: 30px
            @media (max-width: 959px)
              flex: 0 0 100%
              max-width: 100%
              padding: 28px 20px
            .item-title
              font-family: Oswald, sans-serif
              font-weight: 300
              font-size: 30px
              line-height: 120%
              @media (max-width: 1199px)
                font-size: 18px
            .item-adress
              margin-top: 38px
              @media (max-width: 1199px)
                margin-top: 28px
              @media (max-width: 1023px)
                margin-top: 25px
            .phone-link
              display: inline-block
              color: #fff
              margin-top: 10px
              @media (max-width: 1199px)
                margin-top: 15px
              @media (max-width: 1023px)
                margin-top: 10px
            .order-btn
              color: #fff
              padding: 20px 42px
              margin-top: 50px
              position: relative
              @media (max-width: 1199px)
                display: block
                width: 100%
                text-align: center
                padding: 20px 15px
                margin-top: 37px
              @media (max-width: 1023px)
                margin-top: 25px
              @media (max-width: 959px)
                width: auto
                padding: 20px
                position: relative
                left: 5px
              .link-line
                position: absolute
                top: -10px
                left: -7px
                width: calc(100% + 14px)
                height: calc(100% + 14px)
              &:hover
                .link-line
                  path
                    animation: link-arrow .5s cubic-bezier(0.47, 0, 0.745, 0.715) 0s both
          .image-block
            flex: 0 0 220px
            max-width: 220px
            overflow: hidden
            position: relative
            @media (max-width: 1199px)
              flex: 0 0 132px
              max-width: 132px
              overflow: hidden
            @media (max-width: 959px)
              display: none
            &:after
              content: ''
              display: block
              width: 100%
              height: 100%
              position: absolute
              top: 0
              left: 0
              background-color: rgba(235, 171, 154, 0.2)
            .item-image
              display: block
              width: auto
              max-width: 100%
              margin: 0
              transition: transform .7s
              @media (max-width: 1199px)
                height: 100%
                width: auto
                max-width: unset
                position: absolute
                top: 0
                left: 0
          &:hover
            .image-block
              .item-image
                transform: scale(1.15)
  &.restaurant-page
    .main-page__title
      font-family: var(--font-family)
      text-transform: uppercase
      font-size: 80px
      line-height: 120%
      margin-bottom: 35px
      @media (max-width: 1335px)
        font-size: 62px
      @media (max-width: 1199px)
        font-size: 50px
      @media (max-width: 575px)
        font-size: 40px
    .main-page__btn
      display: inline-block
      color: #fff
      position: relative
      padding: 20px 42px
      .link-line
        position: absolute
        top: -10px
        left: -7px
        width: calc(100% + 14px)
        height: calc(100% + 14px)
      &:hover
        .link-line
          path
            animation: link-arrow .5s cubic-bezier(0.47, 0, 0.745, 0.715) 0s both