:root
  --app-height: 100%
html
  margin: 0 !important
*
  box-sizing: border-box
  outline: none
  &::before, &::after
    box-sizing: border-box
body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd
  margin: 0
ul[class], ol[class]
  padding: 0
  list-style-type: none
a
  &:not([class])
    text-decoration-skip-ink: auto
    color: inherit
  &[class]
    text-decoration: none
input, button, textarea, select
  font: inherit
button, label
  cursor: pointer
body
  display: flex
  flex-direction: column
  min-height: 100vh
  min-width: 320px
  position: relative
  font-family: $default-font
  font-weight: 400
  font-size: $default-fz
  line-height: $default-lh
  color: $txt-color
  background-color: $bg-color
  scroll-behavior: smooth
  text-rendering: optimizeSpeed
  overflow-x: hidden
  @include scrollbars($scrl-bar_width, $scrl-bar_track, $scrl-bar_thumb, $scrl-bar_thumbh, 0)
  @media (max-width: 1199px)
    font-size: 14px
    line-height: 22px
  &.overflow
    overflow: hidden
    padding-right: var(--scroll-width)
    .header
      padding-right: var(--scroll-width)
    &.menu-show
      &:before
        content: ''
        display: block
        width: 100%
        height: 100%
        background-color: rgba(#000, .4)
        position: fixed
        top: 0
        left: 0
        z-index: 11
.container
  width: 100%
  margin: 0 auto
  max-width: $container-width
  padding: $container-padding
  @media (max-width: 1199px)
    padding: 0 47px
  @media (max-width: 1023px)
    padding: 0 30px
  @media (max-width: 959px)
    padding: 0 61px
  @media (max-width: 767px)
    padding: 0 20px
  @media (max-width: 639px)
    padding: 0 15px
.preloader
  display: flex
  align-items: center
  justify-content: center
  background-color: #fff
  width: 100%
  height: 100vh
  position: fixed
  top: 0
  left: 0
  z-index: 100
  &__content
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column
    width: 100%
    .logo
      display: block
      width: auto
      max-width: 100%
      margin: 0 auto
      transition: transform .5s, opacity .5s
  &__progress-bar
    width: calc(100% - 30px)
    height: 1px
    max-width: 800px
    background-color: #C2C2C2
    margin: 60px 15px 0
    transition: transform .5s, opacity .5s
    position: relative
    .current-line
      display: block
      height: 3px
      width: 0
      background-color: $accent-color
      position: absolute
      bottom: -1px
      left: 0
      transition: width .5s
  &__percent
    text-align: center
    font-family: Oswald, sans-serif
    font-weight: 300
    font-size: 30px
    line-height: 100%
    color: $txt-color
    margin-top: 45px
    transition: color .3s, transform .5s, opacity .5s
    &.done
      color: $accent-color
  &.hide
    .preloader__content
      .logo
        transform: translateY(-10px)
        opacity: 0
    .preloader__progress-bar
      transform: translateY(-10px)
      opacity: 0
    .preloader__percent
      transform: translateY(-10px)
      opacity: 0


