.icon-delivery_icon:before
  content: '\e803'
.icon-down_arrow:before
  content: '\e804'
.icon-inst_icon:before
  content: '\e805'
.icon-link_arrow:before
  content: '\e806'
.icon-togis_icon:before
  content: '\e807'
.icon-vk_icon:before
