.header
  position: absolute
  top: 0
  left: 0
  width: 100%
  z-index: 10
  background-color: #fff
  &__content
    display: flex
    align-items: center
    justify-content: flex-start
    flex-wrap: wrap
    padding: 25px 0
    max-width: 1044px
    @media (max-width: 1800px)
      max-width: 100%
    @media (max-width: 1199px)
      padding: 15px 0
    @media (max-width: 959px)
      padding: 10px 0
    .logo-link
      display: flex
      align-items: center
      justify-content: flex-start
      flex: 0 0 auto
      width: auto
      min-height: 59px
      @media (max-width: 1199px)
        max-width: 40px
      @media (max-width: 1199px)
        min-height: 42px
      .logo-image
        display: block
        width: auto
        max-width: 100%
        margin: 0
    .main-menu
      margin: 0 auto
      @media (max-width: 1800px)
        margin: 0 0 0 5%
      @media (max-width: 959px)
        position: absolute
        width: 330px
        height: var(--app-height)
        padding: 70px 0
        margin: 0
        top: 0
        right: 0
        transform: translateX(100vw)
        background-color: $bg-color
        transition: transform .3s
        &.show
          transform: translateX(0)
      @media (max-width: 767px)
        width: 265px
      @media (max-width: 575px)
        width: 255px
      &__list
        display: flex
        align-items: center
        justify-content: flex-start
        flex-wrap: wrap
        @media (max-width: 959px)
          flex-direction: column
          align-items: center
          justify-content: center
      &__item
        flex: 0 0 auto
        width: auto
        margin: 0 25px
        position: relative
        @media (max-width: 959px)
          margin: 35px 0 0
          width: 100%
          text-align: center
        .link-line
          position: absolute
          top: calc(50% - 32px)
          left: calc(50% - 80px)
          z-index: -1
          @media (max-width: 1199px)
            max-width: 150%
            left: calc(-25% - 5px)
          @media (max-width: 959px)
            left: calc(50% - 82px)
            top: -22px
          path
            animation: link-arrow .5s cubic-bezier(0.47, 0, 0.745, 0.715) 0s both
            animation-delay: 4s
        &.submenu-item
          .submenu-wrapper
            display: none
            position: absolute
            top: 100%
            left: -30px
            z-index: 1
            padding-top: 40px
            width: auto
            min-width: calc(100% + 60px)
            @media (max-width: 1199px)
              padding-top: 25px
            @media (max-width: 959px)
              position: relative
              top: 0
              left: 0
              width: 100%
              min-width: auto
              border-top: 1px solid rgba($accent-color, .15)
              padding: 0
              margin-top: 20px
          .submenu-list
            background-color: #fff
            padding: 30px 0
            border: 1px solid $bg-color
            position: relative
            @media (max-width: 959px)
              background-color: transparent
              padding: 20px 0
            &:nth-of-type(2)
              border-left: none
            &__item
              white-space: nowrap
              margin-top: 20px
              padding: 0 30px
              &:nth-of-type(1)
                margin-top: 0
              &.submenu-item
                .thirdmenu-wrapper
                  display: none
                  position: absolute
                  left: 100%
                  top: -1px
                  @media (max-width: 959px)
                    position: relative
                    top: 0
                    left: 0
                &:hover
                  & > .submenu-list__link
                    &:before
                      width: 100%
            &__link
              display: inline-block
              color: $txt-color
              transition: color .3s
              position: relative
              &:before
                content: ''
                display: block
                width: 0
                height: 1px
                background-color: $accent-color
                transition: width .3s
                position: absolute
                bottom: 2px
                left: 0
              &:hover
                color: $accent-color
                &:before
                  width: 100%
        &:hover
          .main-menu__link
            &:before
              width: 100%
      &__link
        display: inline-flex
        align-items: center
        justify-content: flex-start
        color: $txt-color
        position: relative
        &:before
          content: ''
          display: block
          width: 0
          height: 1px
          background-color: $accent-color
          position: absolute
          bottom: 2px
          left: 0
          transition: width .3s
        .icon
          font-size: 8px
          line-height: 20px
          margin-left: 15px
        &.active
          &:before
            width: 100%
    .phone-link
      display: inline-block
      color: $txt-color
      font-size: 18px
      line-height: 100%
      margin-left: auto
      transition: color .3s
      @media (max-width: 1199px)
        font-size: 14px
        line-height: 22px
      @media (max-width: 959px)
        position: relative
        z-index: 1
      &:hover
        color: $accent-color
    @import "hamburger"
  & + .main-section
    padding-top: 109px
    @media (max-width: 1199px)
      padding-top: 71px

  &.restaurant-header
    .header__content
      max-width: 100%
      padding: 25px 0
      @media (max-width: 1199px)
        padding: 15px 0
      @media (max-width: 959px)
        padding: 10px 0
      .logo-link
        margin-right: auto
        @media (max-width: 1335px)
          max-width: 72px
      .main-menu
        flex: 1 1 auto
        width: auto
        margin: 0 8.5%
        @media (max-width: 1439px)
          margin: 0 auto
          flex: 0 0 auto
          width: auto
        @media (max-width: 959px)
          width: 330px
          margin: 0
        &__item
          @media (max-width: 1199px)
            margin: 0 20px
          @media (max-width: 1023px)
            margin: 0 15px
          @media (max-width: 959px)
            margin: 30px 0 0 0
          &.delivery-item
            margin-left: auto
            @media (max-width: 1439px)
              margin: 0 25px
            @media (max-width: 959px)
              margin: 30px 0 0 0
.menu-show
  .header
    z-index: 20